@use 'base/variables' as v;
@use 'base/mixins' as m;

.boletos{
    padding: 5rem 0;
    h3{
        text-align: center;
        color: v.$blanco;
        margin-bottom: 2rem;
    }
    .contenedor-boletos{
        @include m.mediaQuery(768px) {
            @include m.grid(2, 5rem);
        }
    }
}

.pase{
    color: v.$negro;
    .nombre-pase{
        text-align: center;
        font-size: 2.4rem;
        color: v.$blanco;
    }
    &-header,
    &-footer{
        padding: 3rem;
        text-transform: uppercase;
        p{
            text-align: center;
            margin: 0;
            color: v.$blanco;
            font-size: 1.6rem;
        }
    }

    &-header{
        background-color: v.$rosa;
    }
    &-body{
        background-color: v.$blanco;
        ul{
            @include m.resetear-lista;
            li,
            :nth-child(odd){
                background-color: v.$blanco;
                padding: 1rem;
                border-bottom: 1px solid #e1e1e1;
            }
        }
    }
    &-footer{
        background-color: v.$morado;
        font-size: 2.4rem;
        font-weight: 700;
    }
}