@use 'base/variables' as v;
@use 'base/mixins' as m;

.video{
    position: relative;
    min-height: 30rem;
    max-height: 50rem;
    overflow: hidden;

    .overlay{
        display: flex;
        flex-direction: column;
        justify-content: center;
        position: absolute;
        width: 100%;
        height: 100%;
        background: rgb(126, 2, 195);
        background: linear-gradient(270deg, rgba(165, 3 ,255 , 0.5) 0%, 
        rgba(255, 0, 110, 0.3) 100%);
    }
    
    video{
        width: auto;
        display: block;
        @include m.mediaQuery(768px) {
            width: 100%;
        }
    }
}

.contenido-video{
    z-index: 10;
    h2{
        color: v.$blanco;
        font-size: 7rem;
        margin: 0;
    }
    p{
        background-color: v.$rosa;
        display: inline-block;
        padding: 1rem 2rem;
        color: v.$blanco;
        font-weight: 700;
        font-size: 2.4rem;
        margin: 0;
    }
}